@import "main-bx";

.intro {
    position: relative;

    &__img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
    }

    &__row {
        position: relative;
        z-index: 10;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 150px 75px 120px 65px;

        @include below(1340px) {
            padding: 120px 40px;
        }

        @include below(992px) {
            padding: 87px 30px 90px;
            flex-direction: column;
            align-items: stretch;
        }

        @include below(479px) {
            padding: 87px 15px 90px;
        }
    }

    &__logo {
        width: 100%;
        max-width: 456px;

        @include below(1340px) {
            max-width: 340px;
        }

        @include below(992px) {
            max-width: 283px;
        }

        @include below(385px) {
            max-width: 200px;
        }

        @include below(370px) {
            max-width: 160px;
        }
    }

    &__right {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;

        @include below_and_above(992px, 541px) {
            align-items: start;
        }

    }

    &__title {
        width: 100%;
        max-width: 575px;

        @include below(1560px) {
            max-width: 460px;
        }

        @include below(1340px) {
            max-width: 340px;
        }

        @include below(992px) {
            max-width: 250px;
            margin-top: 20px;
        }

        @include below(540px) {
            position: absolute;
            top: -60px;
            right: -10px;
            max-width: 168px;
            margin: 0;
        }

        @include below(385px) {
            top: -50px;
            right: 5px;
        }

        @include below(370px) {
            max-width: 120px;
        }
    }

    &__text {
        color: $color_white;
        font-size: 32px;
        font-weight: 400;
        line-height: normal;
        margin: 20px 0 60px;
        position: relative;

        &::before {
            content: "фрибет 3000₽ за регистрацию";
            position: absolute;
            z-index: -1;
            font-size: inherit;
            filter: blur(2px);
        }

        @include below(1340px) {
            font-size: 24px;
            margin: 25px 0 66px;
        }
    }

    &__btn {
        color: $color_white;
        font-size: 32px;
        font-weight: 400;
        line-height: normal;
        border-radius: 50px;
        border: 2px solid #FCFCFC;
        padding: 29px 36px;
        position: relative;
        
        &:hover:not(:active) {
            &::before {
                border-color: #00FF43;
            }
        }

        &::before {
            position: absolute;
            content: "";
            top: -.5%;
            left: -.5%;
            z-index: -1;
            width: 101%;
            height: 101%;
            border-radius: 50px;
            border: 3px solid rgba(0, 255, 67, 0.50);
            transition: border-color .3s linear;
            filter: blur(2px);
        }

        span {
            position: relative;
            &::before {
                position: absolute;
                z-index: -1;
                filter: blur(2px);
                content: "Зарегистриоваться сейчас";
                font-size: inherit;
            }
        }

        &:hover {
            border: 2px solid #FCFCFC;
        }

        @include below(1340px) {
            font-size: 24px;
            padding: 26px 36px;
        }

        @include below(540px) {
            width: 100%;
            padding: 25px 0;
        }
    }
}