@import "main-bx";

.rate {
    background-color: $color_black;
    padding: 169px 0 192px;
    position: relative;

    @include below(768px) {
        padding: 81px 0 132px;
    }

    &__container {
        position: relative;
        z-index: 5;
    }

    &__img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
    }

    &__head {
        margin-bottom: 64px;    

        @include below(768px) {
            margin-bottom: 78px;
        }
    }

    &__title {
        position: relative;
        color: $color_white;
        font-size: 48px;
        font-weight: 700;
        line-height: normal;
        text-transform: uppercase;
        z-index: 9;

        position: relative;

        &::before {
            content: "Выберите подходящий тариф";
            position: absolute;
            z-index: -1;
            font-size: inherit;
            filter: blur(2px);
        }

        @include below(1560px) {
            font-size: 38px;
        }

        @include below(1024px) {
            font-size: 29px;
        }
    }

    &__title-icon {
        position: absolute;
        z-index: 10;
        top: 3px;
        left: 51.8%;
        @include below(1560px) {
            top: -3px;
            left: 41.5%;
        }

        svg {
            width: 75px;
            height: 85px;

            @include below(1560px) {
                width: 50px;
                width: 60px;
            }

            @include below(1024px) {
               width: 28px;
               height: 31px;
            }
        }
    }

    &__row {
        display: flex;
        align-items: stretch;
        margin: 0 -25px;

        @include below(1240px) {
            flex-wrap: wrap;
            margin: -20px -20px 0;
        }

        @include below(640px) {
            padding: 0 100px;
            margin: -20px 0 0;
        }

        @include below(540px) {
            padding: 0 50px;
        }
    }

    &__card  {
        width: calc(33.333% - 50px);
        margin: 0 25px;
        position: relative;
        box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
        border-radius: 37px;

        @include below(1240px) {
            width: calc(50% - 40px);
            margin: 20px 20px 0;
        }

        @include below(640px) {
            width: 100%;
            margin: 20px 0 0;
        }
    }

    &__card-content {
        position: absolute;
        top: 360px;
        left: 50%;
        transform: translate(-50%,-50%);
        color: $color_white;
        display: flex;
        flex-direction: column;
        align-items: center;

        @include below(1500px) {
            top: 270px;
        }

        @include below(1024px) {
            top: 200px;
        }

        @include below(540px) {
            top: 180px;
        }
    }

    &__card-text {
        font-size: 26px;
        font-weight: 400;
        line-height: normal;

        position: relative;

        &::before {
            content: attr(data-text);
            position: absolute;
            z-index: -1;
            font-size: inherit;
            filter: blur(2px);
        }

        @include below(1340px) {
            font-size: 20px;
        }

        @include below(479px) {
            font-size: 14px;
        }
    }

    &__card-name {
        font-family: $second_font;
        font-size: 47px;
        font-weight: 400;
        line-height: normal;
        text-transform: uppercase;
        margin-bottom: 40px;

        position: relative;

        &::before {
            content: attr(data-name);
            position: absolute;
            z-index: -1;
            font-size: inherit;
            filter: blur(2px);
        }

        @include below(1340px) {
            font-size: 38px;
            margin-bottom: 25px;
        }

        @include below(1024px) {
            font-size: 24px;
        }

    }

    &__card-link {
        font-size: 16px;
        font-weight: 400;
        line-height: normal;
        padding: 14px 48px;
        color: $color_white;
        border-radius: 5px;
        border: 1px solid #FFF;
        backdrop-filter: blur(2px);

        position: relative;

        &::before {
            position: absolute;
            content: "";
            top: 0;
            left: 0;
            z-index: -1;
            width: 100%;
            height: 100%;
            border-radius: 5px;
            border: 1px solid #fff;
            filter: blur(2px);
        }

        span {
            position: relative;

            &::before {
                content: "Подробнее";
                position: absolute;
                z-index: -1;
                font-size: inherit;
                filter: blur(2px);
            }
        }

        @include below(1024px) {
            font-size: 14px;
        }

        @include below(479px) {
            font-size: 11px;
            padding: 7px 20px;
        }
    }

    &__bottom {
        margin-top: 277px;
        display: flex;
        justify-content: space-between;

        @include below(1500px) {
            margin-top: 150px;
        }

        @include below(1024px) {
            flex-direction: column;
            margin-top: 100px;
        }
    }

    &__content {
        color: $color_white;
        font-family: 'TT-TravelsNext';
        padding-top: 75px;

        @include below(1240px) {
            padding: 0;
        }
    }

    &__content-title {
        font-size: 42px;
        font-weight: 900;
        line-height: normal;
        margin-bottom: 56px;
        strong {
            text-transform: uppercase;

            position: relative;

            &::before {
                content: "СПЕЦИАЛЬНОЕ ПРЕДЛОЖЕНИЕ";
                position: absolute;
                z-index: -1;
                font-size: inherit;
                filter: blur(2px);
            }

            @include below(540px) {
                font-size: 21px;
            }
        }

        @include below(1560px) {
           font-size: 36px;
        }

        @include below(1450px) {
            font-size: 30px;
        }

        @include below(768px) {
            font-size: 24px;
        }

        span {
            position: relative;

            &::before {
                content: "для новых пользователей";
                position: absolute;
                z-index: -1;
                font-size: inherit;
                filter: blur(2px);
            }
            @include below(540px) {
               font-size: 18px;
            }
        }
    }

    
    &__flex {
        display: inline-flex;
        margin: 0 -5px;

        @include below(640px) {
            display: block;
        }
        svg {
            width: 21px;
            height: 21px;
            margin: 0 5px;
        }
    }

    &__content-flex {
        padding-left: 170px;

        @include below(1240px) {
            padding: 0;
        }
    }

    &__content-item {
        position: relative;
        &:not(:last-child) {
            margin-bottom: 45px;
        }
    }

    &__content-numb {
        font-size: 52px;
        font-weight: 800;
        line-height: normal;
        font-style: italic;
        position: absolute;
        top: -30px;
        left: -50px;
        z-index: 10;

        @include below(1450px) {
            font-size: 40px;
        }

        @include below(1240px) {
            left: 0;
        }

        @include below(1024px) {
            font-size: 30px;
        }

        @include below(540px) {
            font-size: 22px;
        }
    }

    &__content-text {
        position: relative;
        width: 100%;
        max-width: 594px;
        display: inline-block;
        font-size: 24px;
        font-weight: 800;
        line-height: normal;
        text-align: center;
        text-transform: uppercase;
        border-radius: 25px;
        background: rgba(75, 94, 0, 0.25);
        backdrop-filter: blur(2px);
        padding: 48px 0;
        font-style: italic;
        z-index: 9;

        @include below(1450px) {
            max-width: 520px;
        }

        @include below(1240px) {
            max-width: 470px;
        }

        @include below(1024px) {
            font-size: 20px;
            padding: 30px 0;
            margin-left: 60px;
        }

        @include below(570px) {
            margin-left: 20px;
        }

        @include below(540px) {
            max-width: 400px;
            font-size: 16px;
            padding: 10px 8px;
            text-align: start;
            border-radius: 5px;
        }

        @include below(450px) {
            white-space: nowrap;
            max-width: 350px;
            margin-left: 10px;
        }
    }

    &__right {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;

        @include below(1024px) {
            margin-top: 30px;
        }

        @include below(479px) {
            margin-top: 0;
        }
    }

    &__right-icon {
        width: 100%;
        max-width: 482px;

        @include below(1450px) {
            max-width: 400px;
        }

        @include below(479px) {
            max-width: 300px;
            position: absolute;
            top: -25px;
            right: -15px;
        }

    }

    &__right-btn {
        margin-top: 40px;
        padding: 36.5px 116px;
        border-radius: 25px;
        font-size: 48px;
        font-weight: 400;
        line-height: normal;

        position: relative;

        &::before {
            position: absolute;
            content: "";
            top: 0;
            left: 0;
            z-index: -1;
            width: 100%;
            height: 100%;
            border-radius: 25px;
           background: #FEA804;

            filter: blur(2px);
        }

        span {
            position: relative;
            &::before {
                content: "Подробнее";
                position: absolute;
                z-index: -1;
                font-size: inherit;
                filter: blur(2px);
            }
        }

        @include below(1560px) {
            padding: 25px 70px;
        }

        @include below(1240px) {
            font-size: 30px;
        }

        @include below(540px) {
            margin-top: 0;
            font-size: 24px;
            padding: 39px 88px;
        }

        @include below(479px) {
            margin-top: 270px;
        }
    }

}