@import "main-bx";

.personal {
    position: relative;
    padding: 99px 20px 40px;

    @include below(768px) {
        padding: 50px 15px 25px; 
    }

    &__img {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        z-index: 1;
    }

    &__wrap {
        display: flex;
        align-items: flex-start;
        position: relative;
        z-index: 5;
    }

    &__right {
        flex: auto;
    }

    &__left {
        width: 100%;
        max-width: 337px;
        margin-right: 20px;
        border-radius: 25px;
        background: (linear-gradient(90deg, #179B3C 0%, #00721E 100%));
        backdrop-filter: blur(7.5px);
        padding: 38px 25px;

        @include below(1024px) {
            display: none !important;
        }
    }

    &__menu-item {
        &:not(:last-child) {
            margin-bottom: 25px;
        }

        a {
            display: flex;
            align-items: center;
            color: #374957;
            border-radius: 20px;
            background-color: #F9F9F9;
            padding: 20px 25px;
            font-size: 16px;
            font-weight: 500;
            line-height: normal;
            span {
                color: $color_black;
                margin-left: 10px;
            }
        }
    }

    &__title {
        font-size: 48px;     
        font-weight: 700;
        line-height: normal;
        text-transform: uppercase;
        margin-bottom: 27px;

        @include below(768px) {
            font-size: 24px;
            margin-bottom: 25px;
        }
    }

    &__row {
        display: flex;
        align-items: stretch;

        @include below(1440px) {
            display: block;
        }
    }

    &__box {
        border-radius: 25px;
        background: rgba(221, 221, 221, 0.50);
        backdrop-filter: blur(7.5px);
        padding: 35px 35px 46px;
        width: 100%;

        @include below(1024px) {
            padding: 35px 30px;
        }

        &:first-child {
            max-width: 696px;
            margin-right: 20px;

            @include below(1440px) {
                max-width: initial;
                margin-bottom: 25px;
            }
        }

        &:last-child {
            display: flex;
            flex-direction: column;
        }
    }

    &__box-title {
        font-size: 16px;
        font-weight: 700;
        line-height: normal;
        text-transform: uppercase;
        margin-bottom: 25px;
    }


    &__table {

        tr {
            @include below(580px) {
                display: flex;
                flex-direction: column;
            }
        }

        tr.padding {
            td {
                height: 40px;

                @include below(768px) {
                    height: 15px;
                }
            }
        }

        td {
            &:first-child {
                width: 140px;
            }

            &:nth-child(2) {
                padding: 0 24px;

                @include below(768px) {
                    padding: 0 15px;
                }

                @include below(580px) {
                    padding: 0;
                }
            }

            &:last-child {
                width: 100px;
            }
        }
    }

    &__table-currency {
        width: 100%;
        max-width: 120px;
        margin-right: 45px;

        @include below(580px) {
            margin-right: 0;
            margin-bottom: 15px;
            max-width: initial;
        }

        img {
            display: block;
            width: initial;
        }

        &_first {
            display: flex;

            img {
                &:first-child {
                    margin-right: 25px;
                    @include below(580px) {
                        margin-right: 15px;
                    }
                }
            }
        }

        &_second {
            @include below(580px) {
                display: flex;
                flex-wrap: wrap;
            }


            img {
                &:not(:last-child) {
                    margin-bottom: 5px;
                }
            }
        }
    }

    &__table-desc {
        margin-right: 24px;
        font-size: 11px;
        font-weight: 400;
        line-height: normal;

        @include below(580px) {
            margin-right: 0;
            margin-bottom: 15px;
        }

        p {
          margin-bottom: 2px;
        }

        a {
            font-size: 11px;
            color: #000;
            text-decoration: underline;
        }
    }

    &__table-btn {
        margin-left: auto;

        font-size: 16px;
        font-weight: 400;
        line-height: normal;
        text-decoration-line: underline;
        color: #179B3C;
    }

    .personal__box-btn {
        width: 100%;
        max-width: 165px;
        display: block;
        margin: auto auto 0;
        border-radius: 15px;
        box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
        font-style: normal;
    }
}