@import "main-bx";
.header {
    position: relative;
    background: linear-gradient(90deg, #179B3C 0%, #00721E 100%);
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    padding: 15px 20px;
    z-index: 100;

    @include below(479px) {
        padding: 24px 15px;
    }

    &__row {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__nav {
        @include below(1124px) {
            display: none;
        }
    }

    &__list {
        display: flex;
        align-items: center;
        margin: 0 -5px;
    }

    &__list-item {
        margin: 0 5px;
    }

    &__list-link {
        color: $color_white;
        font-size: 14px;
        font-weight: 500;
        line-height: normal;
        text-transform: uppercase;
        transition: underline .3s linear;

        &:hover {
            text-decoration: underline;
        }
    }

    &__logo {
        width: 100%;
        max-width: 92px;
    }

    &__right {
        display: flex;
        align-items: center;
    }

    &__btn {
        border-radius: 5px;
        margin-right: 15px;
        padding: 6px 13px;

        &_first {
            position: relative;
            padding: 6px 13px 6px 26px;
            svg {
                position: absolute;
                left: -8px;
            }
            @include below(1124px) {
                background-color: transparent;
                padding: 0;
                margin-right: 28px;
                span {
                    display: none;
                }
                svg {
                    position: static;
                }
            }
        }
       

        &_second {
            @include below(1124px) {
                display: none;
            }
        }
    }

    &__registr {
        border-radius: 5px;
        font-weight: 700;
        padding: 6px 36px 6px 37px;
        @include below(1124px) {
            display: none;
        }
    }

    &__burger {
        display: none;

        @include below(1124px) {
            display: block;
        }
    }
    &__menu {
        position: fixed;
        top: 70px;
        right: 0;
        width: 100%;
        max-width: 500px;
        padding: 25px 30px 100px;
        border-radius: 0px 0px 25px 25px;
        background: rgba(17, 17, 17, 0.85);
        transform: translateX(100%);
        visibility: hidden;
        overflow-y: auto;
        transition: transform .3s linear, visibility .3s linear, display .3s linear;
        display: none;

        
        @include below(1124px) {
            display: block;
        }

        &.is-show {
            visibility: visible;
            transform: translateX(0);
            max-height: 100vh;
            z-index: 500;
        }

        @include below(670px) {
            max-width: none;
        }

        @include below(479px) {
            top: 88px;
            padding: 25px 15px 40px;
        }

        &-close {
            position: absolute;
            top: 25px;
            right: 15px;
        }

        &-nav {
            margin-bottom: 20px;
        }

        .header__menu-item {
            padding: 20px 0;
            border-bottom: 1px solid #FFF;

            a {
                display: flex;
                align-items: center;
                color: $color_white;
                font-size: 16px;
                font-weight: 500;

                svg {
                    margin-right: 10px;
                }
            }
        }

        .header__nav {
            display: block;
            .header__list {
                flex-direction: column;
                align-items: start;
                padding: 0;
                margin: 0 0 43px;

                .header__list-item {
                    &:not(:last-child) {
                        margin: 0 0 20px;
                    }
                }

                .header__list-link {
                    font-size: 18px;
                }
            }
        }

        .header__btn {
            display: block;
            width: 100%;
            max-width: 180px;
            margin: 0 auto 20px;
            padding: 19px 0 20px;
        }

        .header__registr {
            display: block;
            width: 100%;
            max-width: 180px;
            margin: 0 auto 20px;
            padding: 19px 0 20px;
        }

        .header__menu-btn {
            display: flex;
            align-items: center;
            color: #FF2323;
            font-size: 23px;
            font-weight: 500;
            line-height: normal;
            margin: auto;

            svg {
                margin-left: 10px;
            }
        }
    }

}