@import "main-bx";

.btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 10px 13px;
    background-color: $color_orange;
    color: $color_black;
    font-size: 16px;
    font-style: italic;
    transition: color .3s linear, background-color .3s linear;

    &:hover:not(:active) {
        color: $color_white;
        background-color: $color_orange_second;
    }

    &_green {
        background-color: $color_green;
        color: $color_white;
        font-style: normal;
    }
}