@import "main-bx";

.footer {
    background-color: $color_white;

    &__row {
        position: relative;
        display: flex;
        align-items: center;
        padding: 95px 65px 22px;

        @include below(1570px) {
            flex-wrap: wrap;
            justify-content: space-between;
            padding: 95px 40px 22px;
        }

        @include below(1130px) {
            justify-content: stretch;
        }

        @include below(768px) {
            flex-direction: column;
            align-items: stretch;
        }

        @include below(540px) {
            padding: 50px 15px 20px;
        }
    }

    &__nav {
        display: flex;

        @include below(540px) {
           flex-direction: column;
        }
    }

    &__list {
        margin-right: 37px;

        @include below(540px) {
            margin: 0 0 30px;
        }
    }

    &__list-item {
        &:not(:last-child) {
            margin-bottom: 15px;

            @include below(540px) {
                margin-bottom: 20px;
            }
        }

        a {
            color: $color_black;
            font-size: 16px;
            font-weight: 400;
            line-height: normal;
            transition: text-decoration .3s linear;

            &:hover {
                text-decoration: underline;
            }
        }

    }

    &__info {
        display: flex;
        flex-direction: column;
        margin: -55px 196px 0 0;

        @include below(1885px) {
            margin: -55px 40px 0 0;
        }

        @include below(768px) {
            margin: 30px 0 0;
        }

        @include below(540px) {
            text-align: center;
        }

        a {
            font-size: 16px;
            font-weight: 400;
            line-height: normal;
            color: $color_black;
            transition: text-decoration .3s linear;
            margin-bottom: 10px;

            &:hover {
                text-decoration: underline;
            }
        }
    }

    &__info-media {
        a {
            flex-shrink: 0;
            &:not(:last-child) {
                margin-right: 14px;
            }

            svg {
                width: 40px;
                height: 40px;

                @include below(540px) {
                    width: 72px;
                    height: 72px;
                }
            }
        }
    }

    &__partner {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        max-width: 464px;
        height: 280px;
        background: #D9D9D9;
        margin-right: 87px;

        @include below(1750px) {
            max-width: 300px;
            height: 200px;
        }

        @include below(1360px) {
            display: none;
        }

        span {
            font-size: 38px;
            font-weight: 400;
            line-height: normal;

            @include below(1650px) {
                font-size: 30px;
            }
        }
    }

    &__right {
        display: flex;
        align-items: center;

        @include below_and_above(1520px, 1361px) {
            margin-top: 20px;
        }

        @include below(1130px) {
            margin-top: 15px;
        }

        @include below(540px) {
           justify-content: center;
        }
    }

    &__right-media {
        display: flex;
        flex-direction: column;
        margin-right: 63px;

        @include below(540px) {
            margin: 0;
            position: absolute;
            top: 50px;
            right: 15px;
        }
        a {
            &:not(:last-child) {
                margin-bottom: 5px;
            }

            svg {
                width: 40px;
                height: 40px;
            }
        }
    }

    &__logo {
        width: 100%;
        max-width: 300px;
    }

    &__bottom {
        border-top: 1px solid $color_black;
        padding: 20px;
        font-size: 16px;
        font-weight: 400;
        line-height: normal;

        @include below(540px) {
            padding: 12px 15px 36px;
        }
    }

    &__flex {
        display: flex;
        align-items: center;

        @include below(540px) {
           flex-direction: column;
           align-items: start;
        }
        span {
            border-radius: 5px;
            border: 1px solid #000;
            padding: 7px 15px;
            margin-right: 10px;

            @include below(540px) {
                margin: 0 0 10px;
            }
        }
    }

    &__bottom-text {
        margin-top: 23px; 

        @include below(540px) {
            margin-top: 10px;
        }
    }
}