@import "main-bx";

.fancybox__container {
    .fancybox__backdrop {
        background-color: rgba(0, 0, 0, 0.50);
    }
}

.modal {
    display: none;
    &.fancybox__content {
        padding: 0;
    }

    &.modal_auth.fancybox__content {
        width: 100%;
        max-width: 742px;
        border-radius: 25px;
        
        background: rgba(255, 255, 255, 0.50);
        backdrop-filter: blur(7px);
    
        @include below(768px) {
            max-width: 300px;
        }
    }

    &__box {
        display: flex;

        @include below(768px) {
            display: block;
        }
    }

    &__box-bg {
        max-width: 315px;
        img {
            object-fit: initial;
            width: initial;
            // height: auto;

            border-radius: 25px 0 0 25px;

            @include below(768px) {
                border-radius: 25px 25px 0 0;
            }
        }
    }
    &__form {
        flex: auto;
        padding: 35px 30px;
        max-width: 428px;

        @include below(768px) {

        }
    }

    &__form-head {
        width: 100%;
        display: flex;
        border-radius: 20px;
        background: #F9F9F9;
        padding: 5px 2px;
        margin-bottom: 28px;

        
        button {
            font-size: 16px;
            font-weight: 500;
            line-height: normal;
            padding: 15px 58px;
            border-radius: 20px;
            
            @include below(768px)  {
                padding: 10px 5px;
                width: 50%;
                font-size: 14px;
            }

            &.is-active {
                background: #FEA804;
            }
        }
    }

    &__form-action {
        
    }

    &__form-label {
        position: relative;
        display: block;
        span {
            position: absolute;
            top: 50%;
            right: 25px;
            width: 24px;
            height: 24px;
            transform: translateY(-50%);
            cursor: pointer;

            @include below(768px) {
                width: 18px;
                height: 18px;
                right: 20px;
            }

            svg {
                width: 24px;
                height: 24px;
                @include below(768px) {
                width: 18px;
                height: 18px;
            }
            }
        }

        &:not(:last-child) {
            margin-bottom: 25px;
        }
        input {
            width: 100%;
            border-radius: 20px;
            background: #F9F9F9;
            padding: 20px 40px 20px 25px;

            font-size: 16px;
            font-weight: 500;
            line-height: normal;

            &::placeholder {
                color: #111;
            }


            @include below(768px) {
                font-size: 14px;
                padding: 15px 30px 15px 20px;
            }
        }


    }

    &__form-checkbox {
        
        font-family: 'Roboto', sans-serif;
        font-size: 12px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        display: flex;
        align-items: flex-start;
        color: #111;

        a {
            font-family: 'Roboto', sans-serif;
            color: currentColor;
            font-weight: 500;
            text-decoration: underline;
        }


        span {
            cursor: pointer;
            width: 25px;
            min-width: 25px;
            height: 25px;
            margin-right: 16px;
            background-color: #fff;
            border-radius: 2px;

            display: flex;
            align-items: center;
            justify-content: center;

            &::before {
                content: "";
                width: 15px;
                height: 15px;
                background: #FEA804;
                opacity: 0;
                transition: opacity .3s linear;
            }
        }

        input {
            position: absolute;
            left: -100000%;

            &:checked ~ span {
                &::before {
                    opacity: 1;
                }
            }
        }
    }

    &__form-submit {
        border-radius: 25px;
        background: #FEA804;
        box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);

        font-size: 24px;
        font-style: italic;
        font-weight: 400;
        line-height: normal;
        padding: 20px;
        width: 100%;
        max-width: 259px;
        display: block;
        margin: 25px auto 0;

        @include below(768px) {
            border-radius: 10px;
            font-size: 14px;
            padding: 10px;
            max-width: 132px;
        }
    }
}