@import "main-bx";

$animationSpeed: 40s;

// Animation
@keyframes scroll {
	0% { transform: translateX(0); }
	100% { transform: translateX(calc(-250px * 7))}
}

.infinite-autoplay-carousel {

    &__title {
        font-size: 24px;
        font-weight: 700;
        line-height: normal;
        text-transform: uppercase;
        margin-bottom: 40px;
        padding-left: 20px;
        @include above(769px) {
            br {
                display: none;
            }
        }
        @include below(768px) {
            margin-bottom: 25px;
            padding-left: 15px;
        }
    }
   
    &__block {
        background-color: #111;
        overflow: hidden;
        padding: 26px 0;
    }

    &__track {
        animation: scroll $animationSpeed linear infinite;
		display: flex;
    }

    &__slide {
        white-space: nowrap;

        font-size: 18px;
        font-weight: 700;
        line-height: normal;
        text-transform: uppercase;
        color: #fff;

        margin-right: 40px;
    }
}
