@import "main-bx";

.form {
    
    &__flex {
        display: flex;
        margin: 0 -15px;

        @include below(1024px) {
            margin: 0 -7px;
        }

        @include below(580px) {
            display: block;
            margin: 0;
        } 
    }
    
    &__col {
        width: calc(50% - 30px);
        margin: 0 15px;

        @include below(1024px) {
            width: calc(50% - 17px);
            margin: 0 7px;
        }

        @include below(580px) {
            width: 100%;
            margin: 0;
        }
    }

    &__group {
        &:not(:last-child) {
            margin-bottom: 40px;
        
            @include below(1024px) {
                margin-bottom: 15px;
            }
        }
    }

    &__group-title {
        font-size: 16px;
        font-weight: 700;
        text-transform: uppercase;
        margin-bottom: 15px;
    }

    &__btn {
        width: 100%;
        max-width: 165px;
        display: block;
        margin: 40px auto 0;
        border-radius: 15px;
        box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
        font-style: normal;

        @include below(1024px) {
            margin: 15px auto 0;
        }
    }

    &__icon {
        position: absolute;
        top: 50%;
        right: 12px;
        transform: translateY(-50%);
        width: 20px;
        height: 20px;
        cursor: pointer;
        svg {
            width: 20px;
            height: 20px;
        }
    }

    &__label {
        position: relative;
        display: block;
        &:not(:last-child) {
            margin-bottom: 15px;
        }
    }

    &__input {
        width: 100%;
        padding: 10px 30px 10px 12px;
        background-color: #D9D9D9;
    }
}